@import './breakpoints';

@mixin tablet {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$desktop-width}) {
        @content;
    }
}

@mixin toRem($property, $value) {
    #{$property}: ($value / 16) + rem;
    @debug "desktop-width: #{$property}";

}