.hero-area {
  position: relative;
}
.hero-area .image-div {
  width: 423px;
  width: 100%;
  height: 193px;
  max-width: 100%;
}
.hero-area .image-div img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.hero-area .hero-content {
  position: absolute;
  top: 70%;
  background-color: #1B252C;
  width: 87%;
  height: 150px;
  left: 6%;
}
.hero-area .hero-content .hero-title {
  width: 60%;
  margin: auto;
  color: #ffffff;
  text-align: center;
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/42px var(--unnamed-font-family-source-serif-pro);
  font: normal normal 600 36px/42px Source Serif Pro;
  margin-top: 15px;
  margin-bottom: 15px;
}
.hero-area .hero-content .hero-band {
  background-color: #1F39FF;
  height: 8px;
  width: 102px;
  margin: auto;
}

.Product-listing-container {
  padding-top: 130px !important;
}
.Product-listing-container .breadcrumb {
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
  font: normal normal normal 16px/24px Mulish;
  color: var(--unnamed-color-172026);
  padding: 0 5px;
}
.Product-listing-container .filter-sort {
  margin: 25px 5px;
}
.Product-listing-container .filter-sort .filter-sort-text {
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
  color: var(--gray-d2);
  text-align: left;
  text-decoration: underline;
  font: normal normal 600 16px/20px Mulish;
  margin-left: 5px;
}
.Product-listing-container .filter-sort .filter-sort-result {
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
  color: var(--unnamed-color-172026);
  text-align: center;
  margin: 30px 0;
}
.Product-listing-container .products .product {
  margin-bottom: 15px;
}
.Product-listing-container .products .product .product-image {
  margin: 0 12px;
  height: 190px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.Product-listing-container .products .product .product-description {
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
