  height: 145px;
}
.Product-listing-container .products .product .product-description .product-cost {
  margin-top: 8px;
}
.Product-listing-container .products .product .product-description .heart-icon {
  width: 24px;
  height: 20px;
  background-image: url("../images/heart.svg");
